.divider {
  width: 100%;
  height: 0;
  border: 1px solid #EFEFEF;
}
body {
  font-family: "Hiragino Sans GB", "Microsoft YaHei", "WenQuanYi Micro Hei", sans-serif;
}
* {
  font-family: "Hiragino Sans GB", "Microsoft YaHei", "WenQuanYi Micro Hei", sans-serif;
}
ul {
  list-style: none;
  padding: 0 0;
}
/* stylelint-disable no-duplicate-selectors */
/* stylelint-disable declaration-bang-space-before,no-duplicate-selectors */
/* stylelint-disable declaration-bang-space-before,no-duplicate-selectors */
.jtCropperBoard .jtCropperImageContainer {
  width: 100%;
  height: 500px;
}
.jtCropperBoard .jtCropperImageContainer:after {
  display: block;
  visibility: hidden;
  content: '';
  clear: both;
}
.jtCropperBoard .jtCropperImageContainer .cropperCanvasContainer {
  width: 50%;
  height: 100%;
  float: left;
}
.jtCropperBoard .jtCropperImageContainer .previewContianer {
  width: 50%;
  box-sizing: border-box;
  height: 100%;
  float: left;
  padding: 20px 20px;
}
.jtCropperBoard .jtCropperImageContainer .previewContianer .previewContent {
  height: 90%;
  overflow: hidden;
}
.jtCropperBoard .submitContainer .submitBtn {
  display: block;
  border: 1px solid #1890ff;
  color: #1890ff;
  background-color: white;
  border-radius: 5px;
  cursor: pointer;
  margin: 5px auto;
}
.jtCropperBoard .submitContainer .submitBtn:hover {
  background-color: #1890ff;
  color: white;
}
