.divider {
  width: 100%;
  height: 0;
  border: 1px solid #EFEFEF;
}
body {
  font-family: "Hiragino Sans GB", "Microsoft YaHei", "WenQuanYi Micro Hei", sans-serif;
}
* {
  font-family: "Hiragino Sans GB", "Microsoft YaHei", "WenQuanYi Micro Hei", sans-serif;
}
ul {
  list-style: none;
  padding: 0 0;
}
/* stylelint-disable no-duplicate-selectors */
/* stylelint-disable declaration-bang-space-before,no-duplicate-selectors */
/* stylelint-disable declaration-bang-space-before,no-duplicate-selectors */
.jtFormBoard {
  margin: 0px 0px;
  padding: 0px 0px;
}
.jtFormBoard .jtFormRow {
  padding: 0px;
}
.jtFormBoard .jtFormRow .jtFormItem {
  display: inline-block;
  padding: 0px 0px 16px 0px;
}
.jtFormBoard .jtFormRow .jtFormItem .jtFormLabelWrap {
  vertical-align: middle;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.jtFormBoard .jtFormRow .jtFormItem .jtFormLabelWrap > .labelContent {
  margin-right: 5px;
  position: relative;
  font-size: 14px;
  height: 1em;
}
.jtFormBoard .jtFormRow .jtFormItem .jtFormLabelWrap > .labelContent.required:before {
  display: inline-block;
  font-family: SimSun;
  font-size: 16px;
  color: red;
  margin-right: 4px;
  line-height: 1;
  content: '*';
}
.jtFormBoard .jtFormRow .jtFormItem .jtFormFieldWrap {
  position: relative;
  display: inline-block;
  box-sizing: border-box;
}
.jtFormBoard .jtFormRow .jtFormItem .jtFormFieldWrap > input {
  border: 1px solid #eaeaea;
  border-radius: 3px;
}
.jtFormBoard .jtFormRow .jtFormItem .jtFormFieldWrap > input:hover {
  border-color: #1890ff;
}
.jtFormBoard .jtFormRow .jtFormItem .jtFormFieldWrap > input:focus {
  border-color: #1890ff;
  -webkit-box-shadow: 0px 0px 5px 0px #1890ff;
  -moz-box-shadow: 0px 0px 5px 0px #1890ff;
  -ms-box-shadow: 0px 0px 5px 0px #1890ff;
  -o-box-shadow: 0px 0px 5px 0px #1890ff;
  box-shadow: 0px 0px 5px 0px #1890ff;
}
.jtFormBoard .jtFormRow .jtFormItem .jtFormFieldWrap .jtFormField {
  display: inline-block;
  width: 100%;
}
.jtFormBoard .jtFormRow .jtFormItem .jtFormFieldWrap .jtFormField:hover {
  -webkit-box-shadow: 0px 0px 2px 0px #1890ff;
  -moz-box-shadow: 0px 0px 2px 0px #1890ff;
  -ms-box-shadow: 0px 0px 2px 0px #1890ff;
  -o-box-shadow: 0px 0px 2px 0px #1890ff;
  box-shadow: 0px 0px 2px 0px #1890ff;
}
.jtFormBoard .jtFormRow .jtFormItem .jtFormFieldWrap .fieldCheck {
  color: #00d800;
  position: absolute;
  right: -20px;
  font-size: 14px;
  top: 0px;
  display: none;
}
.jtFormBoard .jtFormRow .jtFormItem .jtFormFieldWrap .errorMessage {
  position: absolute;
  top: 100%;
  color: red;
  font-size: 10px;
  display: none;
}
.jtFormBoard .jtFormRow .jtFormItem .jtFormFieldWrap.correct .errorMessage {
  display: none;
}
.jtFormBoard .jtFormRow .jtFormItem .jtFormFieldWrap.correct .fieldCheck {
  display: inline-block;
}
.jtFormBoard .jtFormRow .jtFormItem .jtFormFieldWrap.error .fieldCheck {
  display: inline-block;
  color: red;
}
.jtFormBoard .jtFormRow .jtFormItem .jtFormFieldWrap.error .fieldCheck:before {
  content: '\F06A';
}
.jtFormBoard .jtFormRow .jtFormItem .jtFormFieldWrap.error .errorMessage {
  display: inline-block;
}
.jtFormBoard .jtFormRow .jtFormItem .jtFormFieldWrap.error .jtFormField {
  border: 1px solid red;
}
.jtFormBoard .jtFormRow .jtFormItem .jtFormFieldWrap.error .jtFormField:focus {
  border: 1px solid red;
  outline-color: red;
  -webkit-box-shadow: 0px 0px 2px 0px red;
  -moz-box-shadow: 0px 0px 2px 0px red;
  -ms-box-shadow: 0px 0px 2px 0px red;
  -o-box-shadow: 0px 0px 2px 0px red;
  box-shadow: 0px 0px 2px 0px red;
}
.jtFormBoard .submitBtnRow {
  width: 100%;
}
.jtFormBoard .submitBtnRow .submitBtn {
  border: 1px solid #1890ff;
  color: #1890ff;
  background-color: white;
  border-radius: 5px;
  cursor: pointer;
  display: block;
  margin: 0px auto;
}
.jtFormBoard .submitBtnRow .submitBtn:hover {
  background-color: #1890ff;
  color: white;
}
